
// import { YMaps } from "react-yandex-maps";
import './Map.css';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';
import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';

let DefaultIcon = L.icon({
  iconUrl: icon,
  shadowUrl: iconShadow
});

L.Marker.prototype.options.icon = DefaultIcon;


function Map() {
  const position = [53.909631, 27.569797]; // заданные координаты

  return (

    <div className='map-container'>
      <div className='zagc'>ЗАГС</div>

      <div className='backgroind-img'></div>
      <div className='white-background'>
        <div className='map-main-place'>
          <div>Дворец гражданских обрядов главного управления юстиции Мингорисполкома</div>
          <div> Минск, Коммунистическая ул., 8</div>
        </div>
      </div>
      <MapContainer
        center={position}
        zoom={18}
        scrollWheelZoom={false}
        style={{ height: '450px', width: "100%" }}
      >
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker position={position}>
          <Popup>
            A pretty CSS3 popup. <br /> Easily customizable.
          </Popup>
        </Marker>
      </MapContainer>
    </div>

  );
}



export default Map;
